<template>
  <swiper :options="swiperOption" ref='swiper'>
    <!-- 下拉 -->
    <div class="mine-scroll-pull-down" v-if="pullDown">
      <me-loading :text="pullDownText" inline ref="pullDownLoading"/>
    </div>
    <swiper-slide>
      <slot></slot>
    </swiper-slide>
    <!-- 上拉 -->
    <div class="mine-scroll-pull-up" v-if="pullUp">
      <me-loading :text="pullUpText" inline ref="pullUpLoading"/>
    </div>
    <div class="swiper-scrollbar" v-if="scrollbar" slot="scrollbar"></div>
  </swiper>
</template>
  
<script>
  import {swiper, swiperSlide} from 'vue-awesome-swiper';
  import MeLoading from 'base/loading';
  import {
    PULL_DOWN_HEIGHT,
    PULL_DOWN_TEXT_INIT,
    PULL_DOWN_TEXT_START,
    PULL_DOWN_TEXT_ING,
    PULL_DOWN_TEXT_END,
    PULL_UP_HEIGHT,
    PULL_UP_TEXT_INIT,
    PULL_UP_TEXT_START,
    PULL_UP_TEXT_ING,
    PULL_UP_TEXT_END
  } from './config';

  export default {
    name: 'MeScroll',
    components: {
      swiper,
      swiperSlide,
      MeLoading
    },
    props: {
      scrollbar: {
        type: Boolean,
        default: true
      },
      data: {
        type: [Array, Object]
      },
      pullDown: {
        type: Boolean,
        default: false
      },
      pullUp: {
        type: Boolean,
        default: false
      }
    },

     watch: {
      data() {
        this.update();
      }
    },
    created() {
      this.init();
    },
    methods: {
      update() {
        this.$nextTick(() => {  //保证DOm渲染完毕传数据
          this.$refs.swiper && this.$refs.swiper.swiper.update();
        });
      },
      scrollToTop(speed, runCallbacks) {
        this.$refs.swiper && this.$refs.swiper.swiper.slideTo(0, speed, runCallbacks);
      },
      init() {
        this.pulling = false;//用来判断下拉加载的状态
        this.pullDownText = PULL_DOWN_TEXT_INIT;
        this.pullUpText = PULL_DOWN_TEXT_INIT;// 没有
        this.swiperOption = {
          direction: 'vertical',//方向  垂直
          slidesPerView: 'auto',//一页里面可以看到几张图片    auto自适应
          freeMode: true,//自由模式   就是可以随边滚动
          setWrapperSize: true,//最外层的swiper-container标签 里面的swiper-wrapper标签 会把计算出来的高度放到自己标签上面去
          scrollbar: {//是否存在
            el: this.scrollbar ? '.swiper-scrollbar' : null,
            hide: true//是否自动隐藏
          },
          on: {
            sliderMove: this.scroll,//swiper提供的一个下拉事件
            touchEnd: this.touchEnd, //swiper提供的一个松手事件    后面的this.touchEnd是直接写的 
            transitionEnd: this.scrollEnd // 向上滑动时监听scrollEnd
          }
        };
      },
      scroll(){//下拉
        const swiper=this.$refs.swiper.swiper;
        // 监控什么时候出现回到顶部

        this.$emit('scroll', swiper.translate, this.$refs.swiper.swiper);

        if(this.pulling){//pulling为true的时候直接返回不执行下拉操作
          return;
        }
        // console.log(swiper)
        // console.log(swiper.translate)
        if(swiper.translate > 0){  //下拉
          if(!this.pullDown){//判断父组件有没有给调用的字标签传pullDown  传了为true显示
            return;
          }
          if(swiper.translate > PULL_DOWN_HEIGHT){//滚动条高度大于100的时候
            this.$refs.pullDownLoading.setText(PULL_DOWN_TEXT_START);
          }else{
            this.$refs.pullDownLoading.setText(PULL_DOWN_TEXT_INIT);            
          }
        }else if(swiper.isEnd){
          if(!this.pullUp){
            return;
          }

         //滚动距离加上显示区域容器的高度减去一个定的值，如果大于所有滚动内容的总高度，那么就表示用户正在上拉页面， 需要需要加载数据了
          //               绝对值      滚动距离        显示区域容器高度           解析字符串,并返回一个整数  (可滚动内容的所有高度)
          const isPullUp = Math.abs(swiper.translate) + swiper.height - PULL_UP_HEIGHT > parseInt(swiper.$wrapperEl.css('height'));
          
          if(isPullUp){
            this.$refs.pullUpLoading.setText(PULL_UP_TEXT_START);
          }else{
            this.$refs.pullUpLoading.setText(PULL_UP_TEXT_INIT);
          }
        } 
      },
      scrollEnd(){
        const swiper = this.$refs.swiper.swiper;
        
        this.$emit('scroll-end', this.$refs.swiper.swiper.translate, this.$refs.swiper.swiper, this.pulling);
        // console.log($refs.swiper.swiper.translate)
      },
      touchEnd(){
        if(this.pulling){//pulling为true的时候直接返回不执行拉操作
          return;
        }
        const swiper=this.$refs.swiper.swiper;
        if(swiper.translate > PULL_DOWN_HEIGHT){
          if(!this.pullDown){
            return;
          }
          this.pulling=true;//现在是在下拉加载状态为true不可执行别的上拉操作
          swiper.allowTouchMove = false; //加载的时候禁止触摸
          swiper.setTransition(swiper.params.speed);//swiper.parmas通过参数找到了speed初始的速度  setTransition设置速度
          swiper.setTranslate(PULL_DOWN_HEIGHT);//过度回到制定的位置
          swiper.params.virtualTranslate = true;//定住不给回弹
          this.$refs.pullDownLoading.setText(PULL_DOWN_TEXT_ING);//改变文字样式
          this.$emit('pull-down', this.pullDownEnd);//触发一个事件告诉你想做什么就做什么    pullDownEnd传一个加载完成后的方法
        } else if (swiper.isEnd) { // 底部
          const totalHeight = parseInt(swiper.$wrapperEl.css('height'));
          const isPullUp = Math.abs(swiper.translate) + swiper.height - PULL_UP_HEIGHT > totalHeight;

          if (isPullUp) { // 上拉
            if (!this.pullUp) {
              return;
            }
            this.pulling = true;
            swiper.allowTouchMove = false; // 禁止触摸
            swiper.setTransition(swiper.params.speed);
            swiper.setTranslate(-(totalHeight + PULL_UP_HEIGHT - swiper.height));
            swiper.params.virtualTranslate = true; // 定住不给回弹
            this.$refs.pullUpLoading.setText(PULL_UP_TEXT_ING);
            this.$emit('pull-up', this.pullUpEnd);
          }
        }
      },
      pullDownEnd(){//下拉加载完成后执行的方法     在父组件home里被传参调用
        const swiper=this.$refs.swiper.swiper;

        this.pulling=false;//加载完成后为false解除限制
        this.$refs.pullDownLoading.setText(PULL_DOWN_TEXT_END);
        swiper.params.virtualTranslate = false;
        swiper.allowTouchMove = true;
        swiper.setTransition(swiper.params.speed);
        swiper.setTranslate(0);
        setTimeout(() => {
          this.$emit('pull-dow-transition-end')
        },swiper.params.speed);
      },
      pullUpEnd() {
        const swiper = this.$refs.swiper.swiper;
        this.pulling = false;
        this.$refs.pullUpLoading.setText(PULL_UP_TEXT_END);
        swiper.params.virtualTranslate = false;
        swiper.allowTouchMove = true;
      }
    }
  };
</script>
  
<style lang="scss" scoped>
// 父亲的高度撑开
.swiper-container {
  overflow: hidden;
  width: 100%;
  height: 100%;
}
// 内容自适应
.swiper-slide {
  height: auto;
}


  .mine-scroll-pull-up,
  .mine-scroll-pull-down {
    position: absolute;
    left: 0;
    width: 100%;
  }
  .mine-scroll-pull-down {
    bottom: 100%;
    height: 80px;
  }

  .mine-scroll-pull-up {
    top: 100%;
    height: 30px;
  }
</style>


