// 约定
export const SUCC_CODE = 0;

export const TIMEOUT = 10000;

export const HOME_RECOMMEND_PAGE_SIZE = 20;

export const jsonpOption = {
  param: 'callback', // 用于指定回调的查询字符串参数的名称（默认为callback）;
  timeout: TIMEOUT // 发出超时错误后多长时间。0禁用（默认为60000）;
};



