// 函数节流
export const debounce = (func, delay = 200) => {
  let timer = null;

  return function (...args) {
    timer && clearTimeout(timer); // 一上来就将上一次的定时器清楚
    timer = setTimeout(() => {
      func.apply(this, args);
    }, delay); // 然后延迟200毫秒再来执行方法
  };
};

// 比如在短时间内触发了大量的事件   并不是一上来就会执行  ；
// 比如说比一次执行的时候延迟了200毫秒   ；
// 第一次还没来得及执行 第二次执行就来了  就把上一次的清楚了  再等200毫秒再开始执行 ；



